export default {
  compents: {
  },
  data () {
    return {
      navList: [
        {
          navImg: require('../../assets/wallet.png'),
          navText: '我的钱包',
          routeName: 'myWallet'
        },
        {
          navImg: require('../../assets/carMessage.png'),
          navText: '车辆信息',
          routeName: 2
        },
        {
          navImg: require('../../assets/parking.png'),
          navText: '停车记录',
          routeName: 'parkingRecord'
        }
      ]
    }
  },
  methods: {
    toLink (routeName) {
      this.$router.push({name: routeName})
    }
  }
}



