data () {
return {
targetParkingNo: 0,
payFn: false,
payVal: '1',
timeVal: ['1小时', '00分钟'],
timeList: [['1小时', '2小时', '3小时', '4小时', '5小时', '6小时', '7小时', '8小时', '9小时', '10小时', '11小时', '12小时'], ['00分钟', '30分钟']],
